.single-page{
    margin-top: 50px;
    margin-bottom: 50px;
}
.product_ceil{
    width: 100%;
    border:1px solid #00000033;
    padding: 20px 0 20px 0;
    text-align: center;
}
.product_ceil img{
    width: 60%;
}

/* Описание */
.product_description{
    padding-left: 10px;
    padding-top: 10px;
    
}
.product_name{
    margin-bottom: 15px;
}
.product_price{
    display: inline-block;
    font-weight: bold;
    font-size: 22px;
    margin-bottom: 15px;
}
.product_about{

}
.btn-product-group{
    margin-top: 10px;
    margin-bottom: 30px;
}
.btn-product-attr{
    background: #fff;
    color:#000;
    /* border:1px solid #007bff; */
}
.btn-product-attr:hover{
    background:#007bff;
    
}
.product-cart-icon{
    margin-right: 5px;
}
.btn-active{
    background-color: #17a2b8;
    color: #fff;
}
.error-message{
    color:red;
    margin-top: 10px;
}

@media screen and (max-width:576px){
    .btn-single-add{
        width: 100%;
    }
}