.card{
    margin-bottom: 20px;
    box-shadow: 0 0 3px 0px #e2e2e2;
    transition: .3s;
}
.card:hover{
    box-shadow: 0 0 12px 0px #e2e2e2;
    transition: .3s;
}
.card a{
    text-align: center;
}
.card-image{
    width:60%;
    height:auto;
    margin:0 auto;
    padding-top: 15px;
}
.btn-add-cart{
    font-size: 14px;
    padding: 5px 10px;
}
.btn-add-cart a{
    color:#fff;
    text-decoration: none;
}

@media screen and (max-width:576px){
    .card{
        width: 80%;
    }
    .btn-add-cart{
        width: 100%;
    }
}
@media screen and (max-width:400px){
    .card{
        width: 90%;
    }
}