.cart{
    margin-top: 30px;
    /* color:#000000b0; */
    /* font-family: 'Francois One', sans-serif; */
}
.cart-title{
    margin-bottom: 20px;
}
.cart-table-head{
    border-top:1px solid #9b9b9ba3;
    border-bottom:1px solid #9b9b9ba3;
    padding: 10px 0 10px 0;
}
.cart-number{
    margin-left: 5px;
}
.cart-table-body{
    /* padding-top: 15px; */
}
.btn-action{
    color:#00000085;
    border:none;
    outline:none;
    font-size: 14px;
    margin-left: 4px;
    border-radius:3px;
    padding: 2px 6px;
    background:transparent;
}
.btn-action:hover{
    color:#000000b7;
}
.cart-action{
    margin-right: 5px;
}
.cart-product{
    padding: 10px 0 10px 0;
    border-bottom:1px solid #9b9b9ba3;
}
.checkout{
    margin-top: 10px;
    margin-bottom: 100px;
}
.total-block{
    text-align: right;
}
.total-price{
    margin-left: 4px;
    font-weight: bold;
}
.btn-checkout{
    font-size: 15px;
    margin-top: 10px;
    padding: 4px 15px
}
.cart-icon{
    margin-right: 6px;
}
