.header{
    padding-top:20px;
    font-family: 'Francois One', sans-serif;
    border-bottom:1px solid rgb(155, 155, 155);
    color:#000000b0;
}
.title_link , .title_link:hover{
    text-decoration: none;
}
.store-title{
    padding-left: 10px;
    color:#000000b0;
}
.menu-store{
    list-style: none;
    margin-top: 10px;
    font-size: 1.125rem;
}
.basket_link , .basket_link:hover{
    text-decoration: none;
}

.menu-store li{
    margin-right: 10px;
}

@media screen and (max-width:500px){
    html{
        font-size: 14px;
    }
    .menu-store{
        padding-left: 15px;
    }
}